div a {
  cursor: pointer; 
}

div.footer {
  background-color: rgb(185, 220, 240);
  padding: 3px;
  top: 10px;
  padding-left: 15px;
  margin: auto;
}

div.content-container {
  width: 95%;
}

div.menu a {
  text-decoration: none;
  color: rgb(0, 62, 121);
  cursor: pointer; 
}

div.menu a:hover {
  text-decoration: underline;
}

div.external-link a {
  text-decoration: none;
  color: rgb(0, 121, 61);
  cursor: pointer; 
}

div.external-link {
  padding: 8px;
  text-align: right;
}

div.external-link a:hover {
  text-decoration: underline;
}

div.menu {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  background-color: rgb(185, 220, 240);
  width: 95%;
  margin: auto;
}

div.menu h1 {
  color: rgb(1, 51, 80);
}

div.content h2 {
    color: rgb(19, 100, 153);
}

div.togglable {
  width: 150px;
  padding: 5px;
  background-color:  rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  cursor: pointer; 
}

div.togglable:hover {
  text-decoration: underline;
}

div.small-button {
  display:inline-block;
  margin-left: 5px;
  width: 50px;
  padding: 3px;
  background-color:  rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  text-align: center;
  cursor: pointer; 

}

div.content {
  left: 10px;
  top: 20px;
  text-align: left;
  padding: 20px;
  margin: auto;
}

div.content img {
  width: 630px;
  height: auto;
}

div.apodImg {
  text-align: left;
}

body {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}